import { Link } from 'react-router-dom';
import { useEffect } from 'react';
import './NavBar.css';

function NavBar(){

  useEffect(()=>{

    let menu_btn = document.querySelector("#menu-btn"),
    page_links = document.querySelector("#page-links");

    const doLinkBold = () => {

      window.setTimeout(() => {

        let pathname = window.location.pathname,
        preactive_link = document.querySelector('#page-links > li > a.active'),
        active_link = document.querySelector(`#page-links > li > a[href='${pathname}']`);
        
        if(preactive_link) preactive_link.classList.remove("active");
        active_link.classList.add("active");

      }, 250);
  
    };

    doLinkBold();

    document.querySelectorAll('#page-links > li > a').forEach(link => {
      link.onclick = () => {
        doLinkBold();
        if(page_links.className === "show"){
          page_links.classList.remove("show");
          menu_btn.children[0].classList.remove("os-20_close");
          menu_btn.children[0].classList.add("os-20_menu-3");
        }
      }
    });

    menu_btn.onclick = () => {

      if(page_links.className === "show"){
        page_links.classList.remove("show");
        menu_btn.children[0].classList.remove("os-20_close");
        menu_btn.children[0].classList.add("os-20_menu-3");
      }else{
        page_links.classList.add("show");
        menu_btn.children[0].classList.remove("os-20_menu-3");
        menu_btn.children[0].classList.add("os-20_close");
      }
      
    };

  }, []);

  return (
    <nav className='nav-bar' >
      <ul id='page-links' >
        <li><Link to='/' >Home</Link></li>
        <li><Link to='/about-me' >About me</Link></li>
        <li><Link to='/projects' >Projects</Link></li>
      </ul>
      <button id="menu-btn" type="button" ><span className="remixicon os-20_menu-3" ></span></button>
    </nav>
  );

}

export default NavBar;