import './Preloader.css';
import { useEffect } from 'react';

function Preloader(){

  useEffect(() => {

    window.onload = () => {

      let preloader = document.querySelector("#preloader");

      preloader.children[0].classList.remove("blink");
      preloader.children[0].classList.add("fill");

      window.setTimeout(() => { preloader.remove(); }, 2000);


      const theme = window.matchMedia('(prefers-color-scheme:dark)'),
      meta_theme = document.querySelector('meta[name="theme-color"]'),
      changeTheme = () => {
        if(theme.matches){
          meta_theme.setAttribute('content', '#050604');
        }else{
          meta_theme.setAttribute('content', '#fcfcfc');
        }
      };

      changeTheme();
      theme.onchange = changeTheme;

    };

  }, []);

  return (
    <div id='preloader' className='preloader' >
      <span className='loader blink' ></span>
    </div>
  )

}

export default Preloader;