import './Projects.css';
import imgClimate from '../imgs/climate.jpeg';
import imgColorContrastChecker from '../imgs/color-contrast-checker.jpeg';
import imgPasswordGenerator from '../imgs/password-generator.jpeg';

function Projects(){

  return (
    <Section1 />
  );

}

function Section1(){

  const project = [
    {
      name: "Climate",
      content: "Simple weather application to display current and forecast weather reports.",
      img: imgClimate,
      href: "https://mysteriousmanin.github.io/climate/"
    },
    {
      name: "Color contrast checker",
      content: "The tool tests the contrast ratio of background and text colors for accessibility. You can use it to visualize different color combinations for your website design that are in compliance with Web Content Accessibility Guidelines (WCAG).",
      img: imgColorContrastChecker,
      href: "https://mysteriousmanin.github.io/color-contrast-checker/"
    },
    {
      name: "Password generator",
      content: "Automatically generates a password based on guidelines that you set to create strong, complex, and unpredictable passwords for each of your accounts.",
      img: imgPasswordGenerator,
      href: "https://mysteriousmanin.github.io/password-generator/"
    }
  ];

  return (
    <section className='projects-section' >
      <div>
        <h1 class='h1' >Projects</h1>
        {project.map(p => {
          let { name, content, img, href } = p;
          return <ProjectCard name={name} content={content} img={img} href={href} />
        })}
      </div>
    </section>
  )

}

function ProjectCard(props){

  let { name, content, img, href } = props;

  return (
    <div id="project-card" class='project-card' >
      <div>
        <h2 class='h2' >{name}</h2>
        <p>{content}</p>
      </div>
      <div style={{ backgroundImage:`url(${img})` }} ></div>
      <span><a href={href} target='_new' >View</a></span>
    </div>
  )

}

export default Projects;